<template>
  <el-container class="heightfull">
    <el-header style="background:#409eff;color:#fff;height:80px;" class="f14">
      <el-row class="heightfull">
        <el-col :span="8" class="df aic heightfull">
          <img src="../../assets/img/logo.png" class="logo" />
        </el-col>
        <el-col :span="16" class="df jcfe aic heightfull">
          <div class="df ffcw aic jcc heightfull mlr20">
            <span>{{userInfo.user_name||"默认昵称"}},您好！</span>
            <span class="mt10 fp" style="max-width:300px;">{{userInfo.role_name}}</span>
          </div>
          <div class="mlr20" @click="backLogin">
            <span class="f14">退出</span>
            <i class="el-icon-arrow-right"></i>
          </div>
        </el-col>
      </el-row>
    </el-header>

    <el-container>
      <el-aside width="200px">
        <!-- <div
          class="w20 df jcc aic"
          @click="isCollapse=!isCollapse"
          style="margin:0 auto;color:#303133;"
        >
          <i :class="isCollapse?'el-icon-arrow-right':'el-icon-arrow-left'"></i>
          <span>{{isCollapse?'展开':'折叠'}}</span>
        </div>-->
        <el-menu class="heightfull" router :default-active="$route.path" :unique-opened="true">
          <el-submenu v-for="item in aside" :key="item.index" :index="item.index">
            <template slot="title">
              <i :class="item.icon"></i>
              <span slot="title">{{item.name}}</span>
            </template>
            <el-menu-item
              v-for="(page_item,page_index) in item.page_list"
              :key="page_index"
              :index="page_item.link"
            >{{page_item.name}}</el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-main class="p20">
        <el-row class="mb10">
          <el-tag
            class="mr10 mt5"
            v-for="(item,index) in links"
            :key="item.url"
            closable
            :type="$route.path==item.url?'danger':''"
            @close="closeTag(item.url,index)"
            @click="back(item.url)"
          >{{item.name}}</el-tag>
        </el-row>
        <transition name="el-zoom-in-center">
          <router-view />
        </transition>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "page",
  data() {
    return {
      userInfo: {},
      aside: [],
      links: [] //路由集合
    };
  },
  created() {
    this.userInfo = JSON.parse(localStorage.getItem("userInfo"));
    this.$post("check/jurisdiction").then(res => {
      this.aside = res;
      if (res.length) {
        this.$router.push({
          path: res[0].page_list[0].link
        });
      }
      this.links.push({
      url: this.$route.path,
      name: this.$route.meta.join(">")
    });
    });
  },
  mounted() {},
  watch: {
    "$route.path"() {
      for (let i of this.links) {
        if (i.url === this.$route.path) return;
      }
      this.links.push({
        url: this.$route.path,
        name: this.$route.meta.join(">")
      });
    }
  },
  methods: {
    //点击关闭tag关闭
    closeTag(url, index) {
      if (this.links.length == 1) return;
      if (this.$route.path == url)
        this.$router.push(this.links[this.links.length - 2].url);
      this.links.splice(index, 1);
    },
    //点击返回
    back(url) {
      if (this.$route.path === url) return;
      this.$router.replace(url);
    },
    //点击登出触发
    backLogin() {
      this.$confirm("确认退出登录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        localStorage.removeItem("userInfo");
        this.$router.replace("/");
      });
    }
  }
};
</script>

<style>
.el-aside {
  overflow: hidden;
}
.logo {
  height: 90%;
}
</style>


